}
.page-enter-active, .page-leave-active {
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.page-leave-active:before {
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}
.page-enter {
  opacity: 0;
}
.page-enter:before {
    top: 0vh;
}
.page-enter-to {
  opacity: 1;
}
.page-enter-to:before {
    top: 100vh;
}
.page-leave {
  opacity: 1;
}
.page-leave-to {
  opacity: 0;
}

